import { React, useState, useEffect } from "react";
import styled from "styled-components";

import { Checkbox, Popover, Button, Tooltip } from "antd";
import { FilterOutlined, PlusOutlined, BookOutlined, SettingOutlined } from "@ant-design/icons"
import 'antd/dist/antd.css';

// imported Components ==========================================
import UpdateNote from "./UpdateNote";

/* ******************************************* */
// [Í≤ΩÍ≥†] ÏûÑÏãúÏ†ÅÏù∏ ÏÇ¨Ïö©ÏûêÏÑ§Ï†ï Î≥¥Í¥ÄÏÜå - ÎÇòÏ§ëÏóê Îã§Î•∏ Î∞©Î≤ïÏúºÎ°ú ÎåÄÏ≤¥ÌïÑÏöî
import DataStorage from "../../../../DataStorage"; 
const NYAM_TYPES = DataStorage("NYAM_TYPES");
const NYAM_TYPES_KEY = DataStorage("NYAM_TYPES_KEY");
// [Í≤ΩÍ≥†] ÏûÑÏãúÏ†ÅÏù∏ ÏÇ¨Ïö©ÏûêÏÑ§Ï†ï Î≥¥Í¥ÄÏÜå - ÎÇòÏ§ëÏóê Îã§Î•∏ Î∞©Î≤ïÏúºÎ°ú ÎåÄÏ≤¥ÌïÑÏöî
